/* eslint-disable @typescript-eslint/no-unsafe-assignment */
import theme, {rhythm} from '@kevinastone/theme-blog';
import {Theme as BlogTheme} from '@kevinastone/theme-blog/theme';
import styled from '@emotion/styled';
import {ThemeProvider} from '@emotion/react';
import {graphql, useStaticQuery} from 'gatsby';
import React from 'react';

import Fixed from './fixed';
import Header from './header';

declare module '@emotion/react' {
  // eslint-disable-next-line @typescript-eslint/no-empty-object-type
  export interface Theme extends BlogTheme {}
}

interface Props {
  children: React.ReactNode;
  header?: React.ReactNode;
}

const Footer = styled(Fixed)`
  text-align: center;
  padding: ${rhythm(1)};
`.withComponent('footer');

const Layout = ({children, header}: Props): React.ReactElement => {
  const {
    site: {
      siteMetadata: {title, author},
    },
  } = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
          author
        }
      }
    }
  `);

  return (
    <ThemeProvider theme={theme}>
      <Header siteTitle={title} />
      <main>
        {header}
        <Fixed>{children}</Fixed>
      </main>
      <Footer>
         {author} {new Date().getFullYear()}, Built with{' '}
        <a href="https://www.gatsbyjs.org">Gatsby</a>
      </Footer>
    </ThemeProvider>
  );
};

export default Layout;
