/* eslint-disable @typescript-eslint/no-explicit-any, @typescript-eslint/no-unsafe-return, @typescript-eslint/no-unsafe-argument, @typescript-eslint/no-unsafe-assignment, unicorn/no-useless-spread */

export default function curry(
  fn: (...args: any[]) => any,
  ...args: any[]
): (...args: any[]) => any {
  return (...nArgs: any[]) => fn(...[...args, ...nArgs]);
}
