import useMeasure from '@kevinastone/use-measure';
import * as React from 'react';
import {
  animated,
  useTransition,
  SpringValue,
  ItemKeys,
  OneOrMore,
} from 'react-spring';

interface Props {
  height: SpringValue<number>;
  children: React.ReactNode;
}

function AccordianItem({
  height,
  children,
  ...props
}: Props): React.ReactElement {
  const [bind, {height: viewHeight}] = useMeasure<HTMLDivElement>();

  return (
    <animated.div
      style={{
        height: height.to({output: [0, viewHeight]}),
      }}
      {...props}
    >
      <animated.div {...bind}>{children}</animated.div>
    </animated.div>
  );
}

type RenderFunc<TItem> = (item: TItem, index: number) => React.ReactNode;

export default function useAccordian<TItem>(
  items: OneOrMore<TItem>,
  keyFunc: ItemKeys<TItem>,
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  wrapperProps: any,
): (render: RenderFunc<TItem>) => React.ReactNode {
  const transitions = useTransition(items, {
    keys: keyFunc,
    enter: {height: 1},
    from: {height: 0},
    leave: {height: 0},
    trail: 50,
  });

  return render =>
    transitions((style, item, _, index) => (
      <AccordianItem {...wrapperProps} {...style}>
        {render(item, index)}
      </AccordianItem>
    ));
}
